import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: {uniIcon},
	data() { 
		return { 
			titleId: '',
			lists:[],
			num:1, 
			colorOn:0,
			colorActive:{},
			colorList:[],
			showModal:false,
			modal:{},
			mainList:[]
		}
	},
	mounted() {
		this.queryTypeTree();
	},
	methods: {
		//获取商品类型
		queryTypeTree:function(){
			var that = this;
			uni.request({
				method: 'GET',
				url: that.websiteUrl + '/goodsType/queryTypeTree', 
				data: {
					
				},
				success: (res) => {
					if(res.data.success){
						that.mainList = res.data.data;
						that.titleId = that.mainList[0].children[0].id;
						that.queryGoodByType();
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
					}
					
				}
			});
		},
		//根据类型获取商品列表
		queryGoodByType:function(){
			var that = this;
			uni.request({
				method: 'GET',
				url: that.websiteUrl + '/goods/queryGoodsList', 
				data: {
					typeId:that.titleId
				},
				success: (res) => {
					if(res.data.success){
						that.lists = res.data.data.rows;
						
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
					}
					
				}
			});
		},
		//查询当前商品的规格
		queryGoodsSpecList:function(goodCode){
			var that = this;
			uni.request({
				method: 'GET',
				url: that.websiteUrl + '/goodsSpec/queryGoodsSpecList', 
				data: {
					goodCode:goodCode
				},
				success: (res) => {
					if(res.data.success){
						that.colorList = res.data.data.rows;
						that.colorActive = that.colorList[that.colorOn];
						// console.log(that.colorActive)
						// console.log(that.colorList)
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
					}
					
				}
			});
		},
		//显示选择规格底部弹框
		showModalClick:function(index){
			var that = this;
			if(index!='s'){
				that.showModal = !that.showModal;
				that.modal = that.lists[index];
				
				that.queryGoodsSpecList(that.modal.goodsCode);
			}else{
				that.showModal = !that.showModal
			}
		},
		//选择规格按钮事件
		colorOnClick:function(index){
			this.colorOn =index
			this.colorActive = this.colorList[this.colorOn];
		},
		//增加规格数量
		addNum:function(){
			this.num=parseInt(this.num)+1
		},
		//减少规格数量
		minusNum:function(){
			if(this.num<1){
				this.num = 0
			}else{
				this.num=parseInt(this.num)-1
			}
		},
		//底部弹框立即购买
		goShop:function(){
			uni.showToast({
				icon:"loading",
				title:"loading..."
			})
			this.insertCart('34cf8b92ad0746a9ab476735e36797e7','1221212',this.modal.goodsCode,this.colorActive.id,this.num,2)
		},
		//底部弹框加入购物车
		addShop:function(){
			if(this.num == 0){
				uni.showToast({
					title:"请选择购买数量！",
					icon:"none"
				})
			}else{
				this.insertCart('34cf8b92ad0746a9ab476735e36797e7','1221212',this.modal.goodsCode,this.colorActive.id,this.num,1)
			}
			
		},
		//加入购物车请求
		insertCart:function(userCode,shopCode,goodsCode,specId,num,type){
			var that = this;
			uni.request({
				method: 'POST',
				header:{"content-type": "application/x-www-form-urlencoded"},
				url: that.websiteUrl + '/cart/insertCart', 
				data: {
					userCode:userCode,
					shopCode:shopCode,
					goodsCode:goodsCode,
					specId:specId,
					num:num
				},
				success: (res) => {
					if(res.data.success){
						that.showModal = !that.showModal
						if(type == '1'){
							uni.showToast({
								title:"加入购物车成功！",
								icon:"none"
							})
							setTimeout(function(){
								uni.hideToast()
							},1500)
						}else{
							uni.navigateTo({
								url:"/pages/shop/shop?type="+type
							})
						}
						
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
					}
					
				}
			});
		},
		
		//侧边栏点击分类事件
		changeItem:function(id){
			this.titleId = id
			this.queryGoodByType();
		},
		//点击跳转到商品详情
		goProduct:function(){
			uni.showToast({
				icon:"loading",
				title:"loading..."
			})
			uni.navigateTo({
				url:"/pages/product/product"
			})
		}
	}
}
